		story:
			"Im a college student taking business management. Studying college was sure pain in the pocket. So while my friend and I are having coffee while doing our research I notice that the caf is looking for a part-timer. I love the coffee and pastry here and for entertainment are the pets. So I said to myself why not apply here part-time so that I can have a side income and help my parents in financials. Gladly they accept me. I was even happier serving others and have the pets adopted by the rightful owner.",
